.container {
  width: 75%;
  max-width: 1000px;
  height: 65%;
  border: 10px solid rgba(192, 35, 61, 0.068);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: hidden;
  box-shadow: 2px 6px 12px #8d4b7a;
  overflow-y: auto; }
  .container .listHeader {
    text-align: center; }
  .container .todoListContainer {
    border: 2px solid goldenrod;
    width: 49.5%;
    position: absolute;
    left: 0; }
    .container .todoListContainer__header {
      color: #164388; }
  .container .doneListContainer {
    width: 49.5%;
    position: absolute;
    right: 0;
    border: 2px solid gold; }
    .container .doneListContainer__header {
      color: #055e2d; }

.submit-container {
  height: 100px;
  width: 100%;
  border: 2px solid burlywood;
  padding-left: 30px;
  padding-top: 20px; }
  .submit-container .addItemInput {
    border-radius: 5px;
    padding: 5px 0;
    margin-right: 10px;
    background: #da4453;
    background: linear-gradient(to right, #89216b57, #da445357);
    background: #ebbf2e;
    color: #131212;
    box-shadow: 0px 4px 10px #d1bd09b0;
    text-shadow: 0px 1px 2px;
    font-size: 16px; }
  .submit-container .submitBtn {
    /* padding: 10px; */
    width: 50px;
    height: 50px;
    border-radius: 50%;
    outline: none;
    background-color: #e9b21b;
    color: #030c14;
    border: none;
    transition: 1s ease-out;
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0); }
    .submit-container .submitBtn:hover {
      background-color: #c4b81c;
      box-shadow: 2px 2px #e9b21b;
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg); }
    .submit-container .submitBtn:active {
      -webkit-transform: scale(1.15);
              transform: scale(1.15); }
    .submit-container .submitBtn:hover .addBtnText {
      color: #7e0501;
      -webkit-animation: btnTextMove 1s cubic-bezier(0.25, 1.04, 0.65, 0.07);
              animation: btnTextMove 1s cubic-bezier(0.25, 1.04, 0.65, 0.07); }
    .submit-container .submitBtn .addBtnText {
      display: block;
      transition: 0.5s ease-out; }

.listItem {
  background-color: rgba(209, 148, 120, 0.596);
  padding: 10px;
  list-style-type: none;
  border: 2px solid sandybrown; }
  .listItem .listItemName {
    /* padding: 5px 35px 5px 5px; */
    display: inline-block;
    min-width: 30%;
    max-width: 60%;
    overflow: hidden;
    padding: 5px;
    border-radius: 5%;
    background: #396afc;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2949ff50, #396afc0c);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: gold;
    font-weight: 600; }
  .listItem .listsBtns {
    transition: 0.3s ease-in-out; }
  .listItem .listDeleteLink {
    /* user-select: none; */
    position: absolute;
    left: 80%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    display: inline-block; }
    .listItem .listDeleteLink:hover {
      -webkit-transform: translateX(-100%) scale(0.93);
              transform: translateX(-100%) scale(0.93); }
  .listItem .listDoneLink {
    position: absolute;
    left: 97%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    display: inline-block; }
    .listItem .listDoneLink:hover {
      -webkit-transform: translateX(-100%) scale(0.93);
              transform: translateX(-100%) scale(0.93); }
  .listItem .listItemDelete {
    /* padding: 5px; */
    border: 2px solid rgba(178, 34, 34, 0.651);
    border-radius: 15%;
    color: black;
    background: rgba(230, 87, 4, 0.753);
    text-align: center;
    padding: 5px; }
  .listItem .listItemDone {
    border: 2px solid rgba(178, 34, 34, 0.651);
    border-radius: 15%;
    color: whitesmoke;
    background-color: rgba(145, 187, 241, 0.534);
    color: black;
    text-align: center;
    padding: 5px; }

li a {
  text-decoration: none;
  padding: 5px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; }

button {
  text-decoration: none;
  padding: 5px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; }

html,
body {
  padding: 0;
  margin: 0;
  height: 100vh;
  background: lightcoral;
  overflow: hidden;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  background: url(/react-todo/static/media/todo-orange.7071c46a.jpg);
  background-size: cover;
}

* {
  box-sizing: inherit;
}

